import { Button, CircularProgress, Grid2 as Grid, Typography } from "@mui/material";
import { useAtom, useAtomValue } from "jotai";
import { gameAtom, isGameInProgressAtom } from "./states";
import { useEffect } from "react";
import UndoMoveButton from "./undoMoveButton";
export default function GameInProgress() {
  const game = useAtomValue(gameAtom);
  const [isGameInProgress, setIsGameInProgress] = useAtom(isGameInProgressAtom);
  useEffect(() => {
    if (game.isGameOver()) setIsGameInProgress(false);
  }, [game, setIsGameInProgress]);
  const handleResign = () => {
    setIsGameInProgress(false);
  };
  if (!isGameInProgress) return null;
  return <Grid container justifyContent="center" alignItems="center" gap={2} size={12} data-sentry-element="Grid" data-sentry-component="GameInProgress" data-sentry-source-file="gameInProgress.tsx">
      <Grid container justifyContent="center" alignItems="center" gap={2} size={12} data-sentry-element="Grid" data-sentry-source-file="gameInProgress.tsx">
        <Typography data-sentry-element="Typography" data-sentry-source-file="gameInProgress.tsx">Game in progress</Typography>
        <CircularProgress size={20} color="info" data-sentry-element="CircularProgress" data-sentry-source-file="gameInProgress.tsx" />
      </Grid>

      <Grid container justifyContent="center" alignItems="center" size={12} data-sentry-element="Grid" data-sentry-source-file="gameInProgress.tsx">
        <UndoMoveButton data-sentry-element="UndoMoveButton" data-sentry-source-file="gameInProgress.tsx" />
      </Grid>

      <Grid container justifyContent="center" alignItems="center" size={12} data-sentry-element="Grid" data-sentry-source-file="gameInProgress.tsx">
        <Button variant="outlined" onClick={handleResign} data-sentry-element="Button" data-sentry-source-file="gameInProgress.tsx">
          Resign
        </Button>
      </Grid>
    </Grid>;
}